<i18n>
{
  "en": {
    "MyWallet": "My wallet",
    "Detail": "Detail",
    "balanceOfAccounts": "The balance of accounts",
    "unit": "￥",
    "Recharge": "Recharge",
    "Deposit": "Deposit",
    "Paid": "Paid",
    "UnpaidDeposit": "Unpaid deposit",
    "Withdrawal": "Withdrawal"
  },
  "zh": {
    "MyWallet": "我的钱包",
    "Detail": "明细",
    "balanceOfAccounts": "余额账户",
    "unit": "元",
    "Recharge": "充值",
    "Deposit": "押金",
    "Paid": "已交押金",
    "UnpaidDeposit": "未交押金",
    "Withdrawal": "提现"
  }
}
</i18n>

<template>
  <div class="wallet">
    <head-top :title='$t("MyWallet")' :go-back="true" @goBackHander="goBackHander">
      <button slot="setRight" class="transaction" @click="$router.push({'path':'/transaction'})">{{ $t("Detail") }}</button>
    </head-top>
    <div class="container">
      <div class="column">
        <img src="../../assets/wallet_images/bag.png" alt="" class="bag">
        <div class="balance">
          <p class="tips">{{ $t("balanceOfAccounts") }}</p>
          <p class="count">
            <span>
              {{count}}
              <strong>{{ $t("unit") }}</strong>
            </span>
          </p>
        </div>
        <button @click="$router.push({'path':'/recharge'})">{{ $t("Recharge") }}</button>
      </div>
      <div class="column" @click="$router.push({'path':'/deposit'})">
        <div class="left">{{ $t("Deposit") }}</div>
        <div class="right">
          <span v-if="deposit">{{ $t("Paid") }}</span>
          <span class="red" v-else>{{ $t("UnpaidDeposit") }}</span>
          <img src="../../assets/global_images/next.png" alt="">
        </div>
      </div>
      <div :class="['column', count? 'active': '']" @click="withdraw" >
        {{ $t("Withdrawal") }}
      </div>
    </div>
  </div>
</template>

<script>
import headTop from '../../components/Header/index'
export default {
  name: 'wallet',
  data () {
    return {
      deposit: false, // 是否已交押金
      count: 100
    }
  },

  components: {
    headTop
  },

  methods: {
    goBackHander () {
      // console.log(123)
    },
    withdraw () {
      if (!this.count) {
        return false
      }
      this.$router.push({'path': '/withdraw'})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.wallet{
  .fs;
  background: #FFF;
  .transaction{
    background: #FFF;
    .wh(100px,100%);
    float: right;
    margin-right: 10%;
  }
  .container{
    .fs-noTop;
    .column{
      .wh(690px, 480px);
      margin: 30px auto 0 auto;
      position: relative;
      border-radius: 20px;
      box-shadow: 0px 4px 25px rgba(130,130,130,0.2); // 左上下右
      .bag{
        height: 160px;
        margin-top: 30px;
      }
      .balance{
        padding-top: 30px;
        .tips{
          color: #999999;
          font-size: 30px;
        }
        .count{
          font-size: 30px;
          span{
            font-size: 80px;
            font-family: "Helvetica Neue";
            position: relative;
            strong{
              position: absolute;
              bottom: 10px;
              right: -70px;
              font-size: 30px;
            }
          }
        }
      }
      button{
        .wh-l(480px, 80px);
        background: @themeColor;
        color: #585858;
        position: absolute;
        left: 50%;
        bottom: -40px;
        margin-left: -240px;
        border-radius: 40px;
        font-size: 30px;
      }
    }
    .column:nth-child(2){
      height: 90px;
      line-height: 90px;
      margin-top: 100px;
      display: flex;
      .left{
        .wh(40%,100%);
        line-height: 100px;
        padding-left: 33px;
        text-align: left;
        font-size: 30px;
      }
      .right{
        flex: 1;
        height: 100%;
        line-height: 100px;
        text-align: right;
        padding-right: 30px;
        position: relative;
        span{
          .wh(auto, 100%);
          line-height: 90px;
          color:#cccccc;
          font-size: 26px;
          padding-right: 33px;
          display: block;
          &.red{
            color: red;
          }
        }
        img{
          height: 26px;
          position: absolute;
          top: 50%;
          right: 30px;
          margin-top: -13px;
        }
      }
    }
    .column:nth-child(3){
      .wh-l(690px,100px);
      font-size: 30px;
      background: #aaaaaa;
      color: #ffffff;
      position: absolute;
      left: 50%;
      margin-left: -345px;
      bottom: 50px;
      border-radius: 10px;
      &.active{
        background: #f4da21;
      }
    }
  }
}
</style>



